.about {
  background-color: var(--darker);
  height: calc(100vh - 45px);
  overflow: hidden;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}


#innerAbout {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(66, 66, 66, 0.808);
  padding: 10px;
}

#about-article {
  width: 900px;
  height: 700px;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-image: url("../../assets/images/eli-full.JPEG");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

#about-article h3 {
  color: white;
  font-family: "Prompt", Helvetica, sans-serif;
  font-size: 100px;
  font-weight:900
}

#about-article h4 {
  color: var(--meddark);
  font-family: "Prompt", Helvetica, sans-serif;
  font-size: 20px;
  margin-top: -3px;
  padding-bottom: 0px;
  margin-left: 30px;
}

#about-article div {
  margin-top: 0;
  color: white;
  font-family: "Prompt", Helvetica, sans-serif;
  font-weight: light;
  margin-left: 0;
  width: 70vw;
  margin-right: 0;
  font-size: 30px;
}

.aboutContent {
  overflow-wrap: break-word;
  opacity: 1;
}

@media screen and (max-width: 768px) {
  #about-article div {
    margin-left: 0;
    width: 60%;
    margin-right: 0;
    font-size: 2.4vw;
  }
}

@media screen and (max-width: 500px) {
    #about-article div {
      margin-left: 0;
      width: 60%;
      margin-right: 0;
      font-size: 3.4vw;
    }
  }
